import React, { useEffect, useRef, useState } from 'react';
import '../opghegedusic.css';
import { useLocation } from 'react-router-dom';

function Home() {
  const [isAnimated, setIsAnimated] = useState(false);
  const [showContent, setShowContent] = useState(false);
  const location = useLocation();
  const firstLoad = useRef(true); // Ref za praćenje prvog učitavanja

  useEffect(() => {
    // Provjera je li stranica prvi put učitana ili je došlo do refresh-a
    if (firstLoad.current) {
      // Ako je stranica prvi put učitana ili osvježena
      setIsAnimated(true); // Pokrećemo animaciju
      firstLoad.current = false; // Nakon prvog učitavanja, postavljamo na false
    } else {
      setShowContent(true); // Ako je već bila učitana, odmah prikazujemo sadržaj
    }
  }, [location]); // Praćenje promjena URL-a pomoću useLocation hooka

  return (
    <>
    <div className={`home-page ${isAnimated ? 'fadeIn' : ''}`} style={{ opacity: showContent || isAnimated ? 1 : 0 }}>
          <img src="https://i.ibb.co/Fk2SYZBy/ljesnjak.jpg" alt="ljesnjak" border="0" className='bannerImg'/>
          <h2>
    <strong>OPG Hegedušić </strong> <br/>
    </h2>
    <p>
     Kvalitetan uzgoj lješnjaka s ljubavlju i pažnjom
Dobrodošli na službenu stranicu OPG-a Hegedušić, obiteljskog poljoprivrednog gospodarstva specijaliziranog za uzgoj lješnjaka. Naša obitelj dugi niz godina njeguje tradiciju i strast prema poljoprivredi, a naš primarni cilj je pružiti vam visokokvalitetne, svježe i zdrave plodove koji dolaze iz našeg vlastitog uzgoja.
</p>
<h4>
  <strong>
Kvaliteta koja dolazi iz prirode
</strong>
</h4>
<p>
 <br/>
Lješnjak je više od običnog ploda – to je simbol zdravlja, kvalitete i tradicije. Naša plantaža lješnjaka smještena je u srcu prirode, gdje u sinergiji s okolišem njegujemo svaki korak uzgoja. Na OPG-u Hegedušić posvećeni smo održivom uzgoju, bez upotrebe kemijskih sredstava, kako bismo osigurali da naši lješnjaci budu najkvalitetniji i ekološki prihvatljivi.
 <br/>
 <strong><i>
Zašto odabrati nas?
</i></strong>
<br/>
Prirodni uzgoj – Naši lješnjaci rastu u zdravom, kontroliranom okruženju, daleko od pesticida i kemikalija.
Obiteljska tradicija – Posvećeni smo očuvanju obiteljske tradicije i kvalitetnog poljoprivrednog rada.
Svježina i kvaliteta – Svaki lješnjak koji dođe do vas, prošao je rigoroznu kontrolu kvalitete kako bi osigurao najbolje plodove na tržištu.
Održivo gospodarstvo – Sposobni smo proizvoditi na način koji čuva okoliš i pridaje važnost očuvanju prirodnih resursa.
</p>
<h4>
  <strong>
Naša misija
</strong>
</h4>
<p>
Naša misija je obogatiti tržište najboljim lješnjacima, koji nisu samo ukusni, već i bogati hranjivim tvarima. Želimo pružiti proizvode koji čine razliku i potiču zdravlje naših kupaca, uz očuvanje prirodnih vrijednosti našeg kraja.
</p> 
        
    </div>
    
    </>
  );
};

export default Home;
